@use "../base/variables" as v;
@use "../base/mixins" as m;

.cd-header{
    position: fixed;
	width: fit-content;
	top:0;
	left:0;
	z-index:100;
} 
.header-wrapper{
    position: relative;
	width: fit-content;
	margin-left: 3rem;
} 
.nav-but-wrap{ 
	position: relative;
	display: block;
	float: left;
	padding-left: 15px;
	padding-top: 15px;
	margin-top: 26px;
	transition : all 0.3s ease-out;
}
.menu-icon {
	height: 30px;
	width: 30px;
	position: relative;
	z-index: 2;
	cursor: pointer;
	display: block;
}
.menu-icon__line {
	height: 2px;
	width: 30px;
	display: block;
	background-color: v.$neutral_12;
	margin-bottom: 7px;
	cursor: pointer;
	-webkit-transition: background-color .5s ease, -webkit-transform .2s ease;
	transition: background-color .5s ease, -webkit-transform .2s ease;
	transition: transform .2s ease, background-color .5s ease;
	transition: transform .2s ease, background-color .5s ease, -webkit-transform .2s ease;
}
.menu-icon__line-left {
	width: 16.5px;
	-webkit-transition: all 200ms linear;
	transition: all 200ms linear;
}
.menu-icon__line-right {
	width: 16.5px;
	float: right;
	-webkit-transition: all 200ms linear;
	-moz-transition: all 200ms linear;
	-o-transition: all 200ms linear;
	-ms-transition: all 200ms linear;
	transition: all 200ms linear;
}
.menu-icon:hover .menu-icon__line-left,
.menu-icon:hover .menu-icon__line-right {
	width: 30px;
}
.nav {
	position: fixed;
	z-index: 98;
}
.nav:before, .nav:after {
	content: "";
	position: fixed;
    top: 20px;
    left: 3rem;
	width: 0;
	height: 0;
	background-color: rgba(20, 21, 26,0.6);
	border-bottom-right-radius: 70rem;
	z-index: -1;
	transition: border-radius linear 0.8s, width cubic-bezier(0.77, 0, 0.175, 1) 0.6s, height cubic-bezier(0.77, 0, 0.175, 1) 0.6s;
}
.nav:after {
	background-color: v.$neutral_12;
	-webkit-transition-delay: 0s;
    transition-delay: 0s;
    box-shadow: 6px 7px 28px 0 rgba(16, 16, 16, 0.3);
}
.nav:before {
	-webkit-transition-delay: .2s;
    transition-delay: .2s;
}
.nav__content {
	position: fixed;
	visibility: hidden;
	top: 75px;
    left: 3rem;
	width: 80vw;
	text-align: left;

	@include m.telefono {
		width: 260px;
	}
}
.nav__list {
	position: relative;
	max-height: 50vh;
	padding: 0;
	margin: 0;
	overflow-y: auto;
	scrollbar-width: none;
    -ms-overflow-style: none;
	z-index: 2;
}
.nav__list-item {
	position: relative;
	display: block;
	-webkit-transition-delay: 0.8s;
    transition-delay: 0.8s;
	opacity: 0;
	text-align: left;
	color: #fff;
	overflow: hidden; 
	font-family: 'Poppins', sans-serif;
	line-height: 1.2;
	letter-spacing: 0;
	-webkit-transform: translate(30px, 0%);
    transform: translate(30px, 0%);
	-webkit-transition: opacity .2s ease, -webkit-transform .3s ease;
	transition: opacity .2s ease, -webkit-transform .2s ease;
	transition: opacity .2s ease, transform .2s ease;
	transition: opacity .2s ease, transform .2s ease, -webkit-transform .2s ease;
    margin-top: 7px;
    margin-bottom: 7px;
}
.nav__list-item .target{ 
	position: relative;
	display: flex !important;
	align-items: center;
	gap: 1rem;
	text-decoration: none;
	color: v.$neutral_07;
	overflow: hidden; 
	cursor: pointer;
    font-size: 1.8rem;
	font-weight: 500;
	z-index: 2;
    padding-left: 40px;
    padding-top:5px;
    padding-bottom: 5px;
	display: inline-block;
    -webkit-transition: all 200ms linear;
    transition: all 200ms linear;
}
.nav__list-item .target:after{ 
	position: absolute;
	content: '';
	top: 50%;
	left: 0;
	width: 5px;
	height: 0;
    border-radius: .5rem;
	opacity: 0;
	background-color: v.$main-2;
	z-index: 1;
    -webkit-transition: all 200ms linear;
    transition: all 200ms linear; 
}
.nav__list-item .target:hover:after{ 
	height: 100%;
	opacity: 1;
	top: 0;
}
.nav__list-item .target:hover{
	color: rgba(255,255,255,1);

	p {
		color: v.$neutral_05;
	}
}
.nav__list-item.active-nav .target{
	color: v.$main-1;
}
.nav__list-item.active-nav .target:after{ 
	height: 100%;
	opacity: 1;
	top: 0;
}

.nav__list-item .menu {
	align-items: start;
	flex-direction: column;
	gap: 0;
}

.caracteristica-menu {
	display: flex;
	align-items: center;
	gap: 1rem;
}

.submenu {
    max-height: 0;
    overflow: hidden;
    transition: all .5s ease-in-out;
}

.submenu.active {
	width: 90%;
    max-height: 500px; /* Ajusta según el contenido */
}

.submenu ul {
    list-style: none;
	// width: 18rem;
	width: 100%;
    padding: 10px 0;

	@include m.telefono {
		width: 20rem;
	}
}

.submenu li p {
    display: block;
	width: 100%;
    padding: .8rem .5rem;
    color: v.$neutral_06;
	font-size: 1.2rem;
    text-decoration: none;
    transition: background-color 0.3s;
	border-bottom: .1rem solid v.$neutral_11;
	transition: all .3s ease;
}

.submenu li p:hover {
	border-bottom: .1rem solid v.$neutral_10;
}

.submenu {

	li {
		background: #292929;
		border-radius: .7rem;
		margin-bottom: 0.5rem;

		&:hover {

			p {
				border-bottom: .1rem solid v.$neutral_10;
				color: v.$neutral_00 !important;

				&:nth-child(even) {
					color: rgb(236, 203, 245) !important;
				}
			}
		}

		p {
			border: none !important;
	
			// &:nth-child(odd) {
			// }

			&:nth-child(even) {
				color: v.$main-2 !important;
			}
		}

		&.pregunta-item {

			.respuesta {
				color: v.$neutral_09 !important;
			}
		}
	}
}

.redes-menu {
	display: flex;
	align-items: center;
	gap: .5rem;
	margin-top: 1rem;

	button {
		display: flex;
		align-items: center;
	}
}

body.nav-active .nav__content {
	visibility: visible;
}
body.nav-active .menu-icon__line {
	background-color: #fff;
	-webkit-transform: translate(0px, 0px) rotate(-45deg);
          transform: translate(0px, 0px) rotate(-45deg);
}
body.nav-active .menu-icon__line-left {
	width: 15px;
	-webkit-transform: translate(2px, 4px) rotate(45deg);
          transform: translate(2px, 4px) rotate(45deg);
}
body.nav-active .menu-icon__line-right {
	width: 15px;
	float: right;
	-webkit-transform: translate(-3px, -3.5px) rotate(45deg);
          transform: translate(-3px, -3.5px) rotate(45deg);
}
body.nav-active .menu-icon:hover .menu-icon__line-left,
body.nav-active .menu-icon:hover .menu-icon__line-right {
	width: 15px;
}
body.nav-active .nav {
	visibility: visible;
}
body.nav-active .nav:before, body.nav-active .nav:after {
	width: 80vw;
	height: 60vh;
	border-radius: 15px;

	@include m.telefono {
		width: 260px;
	}
}
body.nav-active .nav:after {
	-webkit-transition-delay: .1s;
          transition-delay: .1s;
}
body.nav-active .nav:before {
	-webkit-transition-delay: 0s;
          transition-delay: 0s;
}
body.nav-active .nav__list-item {
	opacity: 1;
	-webkit-transform: translateX(0%);
          transform: translateX(0%);
	-webkit-transition: opacity .3s ease, color .3s ease, -webkit-transform .3s ease;
	transition: opacity .3s ease, color .3s ease, -webkit-transform .3s ease;
	transition: opacity .3s ease, transform .3s ease, color .3s ease;
	transition: opacity .3s ease, transform .3s ease, color .3s ease, -webkit-transform .3s ease;
}
body.nav-active .nav__list-item:nth-child(0) {
	-webkit-transition-delay: 0.7s;
          transition-delay: 0.7s;
}
body.nav-active .nav__list-item:nth-child(1) {
	-webkit-transition-delay: 0.8s;
          transition-delay: 0.8s;
}
body.nav-active .nav__list-item:nth-child(2) {
	-webkit-transition-delay: 0.9s;
          transition-delay: 0.9s;
}
body.nav-active .nav__list-item:nth-child(3) {
	-webkit-transition-delay: 1s;
          transition-delay: 1s;
}
body.nav-active .nav__list-item:nth-child(4) {
	-webkit-transition-delay: 1.1s;
          transition-delay: 1.1s;
}
body.nav-active .nav__list-item:nth-child(5) {
	-webkit-transition-delay: 1.2s;
          transition-delay: 1.2s;
}
body.nav-active .nav__list-item:nth-child(6) {
	-webkit-transition-delay: 1.3s;
          transition-delay: 1.3s;
}
body.nav-active .nav__list-item:nth-child(7) {
	-webkit-transition-delay: 1.4s;
          transition-delay: 1.4s;
}
body.nav-active .nav__list-item:nth-child(8) {
	-webkit-transition-delay: 1.5s;
          transition-delay: 1.5s;
}
body.nav-active .nav__list-item:nth-child(9) {
	-webkit-transition-delay: 1.6s;
          transition-delay: 1.6s;
}
body.nav-active .nav__list-item:nth-child(10) {
	-webkit-transition-delay: 1.7s;
          transition-delay: 1.7s;
}

body.nav-active .contenedor-maqueta {
    filter: blur(2px);
    transition: filter 0.3s ease;
}