@use "../base/variables" as v;
@use "../base/mixins" as m;

.categoria-acordeon {
  margin-bottom: 1rem;
  width: 100%;
  height: fit-content;

  .categoria-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: transparent;
    border-radius: 1rem;
    border: none;
    border-bottom: .1rem solid v.$neutral_03;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: v.$main-2;

      // .categoria-info {

      //   h3 {
  
      //     svg {
      //       stroke: #fff;
      //     }
      //   }
      // }

      svg {
        stroke: v.$main-1;
      }
    }

    &.activo {
      background-color: v.$main-2;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;

      // .categoria-info {

      //   h3 {

      //     svg {
      //       stroke: #fff;
      //     }
      //   }
      // }

      svg {
        transform: rotate(0deg) !important;
        stroke: v.$main-1;
      }
    }

    .categoria-info {
      position: relative;
      display: flex;
      align-items: center;
      gap: .5rem;
      
      .contador-seleccionados-categoria {
          display: flex;
          justify-content: center !important;
          align-items: center !important;
          background-color: v.$main-4;
          color: v.$main-2;
          border-radius: 50%;
          width: 1.8rem;
          height: 1.8rem;
          margin: 0;
          padding: 0;
          font-size: .8rem;
          font-weight: bold;
          opacity: 0;
          transform: scale(0);
          transition: opacity 0.3s, transform 0.3s cubic-bezier(0.68, -0.55, 0.27, 1.55);

          &.mostrar {
            opacity: 1;
            transform: scale(1);
          }
      }
  
      h3 {
        display: flex;
        align-items: center;
        gap: .5rem;
        margin: 0;
        font-size: 1.4rem;
        font-weight: 600;
        letter-spacing: 0;
        text-transform: uppercase;
        color: v.$neutral_10;
        font-family: v.$fuente_principal;
        transition: color 0.3s ease; 

        svg {
          transform: rotate(0);
          // stroke: #c559db;
          stroke: #af3dc6;
          fill: #fae7ff;
        }
      }
    }

    svg {
      transform: rotate(45deg);
      transition: all 0.3s ease;
      stroke: v.$main-2;
    }
  }

  .servicios-container {
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    margin-top: 0;
    padding: 0 1rem;
    z-index: 0;
    transition: all .4s ease;
    display: grid !important;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: .5rem;

    &.activo {
      display: grid !important;
      max-height: 1000px !important; /* Ajusta este valor según la cantidad máxima de servicios */
      opacity: 1;
      margin-top: 1rem;
      transition: all .4s ease-in-out;
    }
  }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}