@use "../base/variables" as v;
@use "../base/mixins" as m;

.paso-3 {
    align-items: flex-start;
    gap: 1rem;
    
    @include m.tablet {
        flex-direction: row !important;
    }

    .seccion-fechas {
        width: 100%;
        @include m.telefono {
            min-width: 40rem;
        }

        .contenedor-calendario {
            display: none;
            width: 100%;
            margin: 0 auto;
            background: white;
            border-radius: 12px;
            padding: 1rem;

            @include m.tablet {
                max-width: 40rem;
            }

            .calendario-header {
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 1rem;

                h3 {
                    font-family: v.$fuente_principal;
                    margin: 0;
                    font-size: 1.7rem;
                    font-weight: 500;
                    color: v.$neutral_12;
                    letter-spacing: 0;
                    text-transform: none;

                    span {
                        // font-weight: 500;
                        // color: v.$neutral_09;
                        color: v.$main-3;
                    }
                }

                .calendario-buttons {

                    .btn-calendario-prev,
                    .btn-calendario-next {
                        background: none;
                        border: none;
                        cursor: pointer;
                        padding: 0.5rem;
                        border-radius: 50%;
                        stroke: v.$neutral_04;
                        transition: background-color 0.3s;

                        &:hover {
                            background: v.$main-1;
                            stroke: v.$neutral_10;
                        }
                    }
                }
            }

            .calendario-dias-semana {
                display: grid;
                grid-template-columns: repeat(7, 1fr);
                text-align: center;
                font-weight: 600;
                margin-bottom: 0.5rem;
                color: v.$neutral_10;
            }

            .calendario-dias {
                display: grid;
                grid-template-columns: repeat(7, 1fr);
                gap: 0.25rem;

                .dia-calendario {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 4rem;
                    height: 4rem;
                    padding: 1rem;
                    padding-bottom: .9rem;
                    text-align: center;
                    border-radius: 50%;
                    cursor: pointer;
                    transition: all 0.3s;
                    border: 1px solid transparent;

                    @include m.telefono {
                        width: 5rem;
                        height: 5rem;
                    }

                    &:hover {
                        background-color: v.$neutral_03;
                    }

                    &.disponible {
                        color: #333;
                        font-weight: bold;

                        &.seleccionado {
                            background-color: v.$main-3;
                            color: v.$main-1;
                        }
                    }

                    &.no-disponible, &.fuera-de-mes {
                        color: v.$neutral_06;
                        cursor: not-allowed;
                    }

                    &.hoy {
                        border: .2rem solid v.$main-2;
                    }
                }
            }
        }
    }

    .seccion-horarios {
        width: 100%;
        margin: 0 auto;

        @include m.telefono {
            max-width: 40rem;
        }
        @include m.tablet {
            max-width: 21.5rem;
            // max-height: 45rem;
        }

        .contenedor-horarios {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            gap: .5rem;
            padding-top: 0.5rem;
            overflow-y: auto;
            scrollbar-width: none;
            -ms-overflow-style: none;

            @include m.tablet {
                max-height: 29rem;
            }
        }
    }
}

/* En CSS, agregar estilos para horarios ocupados */
.flotante.horario.ocupado {
    opacity: 0.5;
    cursor: not-allowed;
    position: relative;
}

.flotante.horario.ocupado::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 10%;
    right: 10%;
    height: 2px;
    background: var(--color-error);
    transform: rotate(-45deg);
}