@use "../base/variables" as v;
@use "../base/mixins" as m;

nav {
    position: fixed;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: v.$navbar;
    background-color: transparent;
    color: v.$main-4;
    text-align: center;
    font-size: 1rem;
    margin-top: 0;
    padding: .5rem 0;

    .logo {
        position: relative;
        margin: 0;
        font-family: v.$fuente_cursiva;
        font-weight: 900;
        font-size: 3rem;
        color: v.$neutral_12;
        text-transform: lowercase;

        @include m.telefono {
            font-size: 3.5rem;
        }

        span {
            position: absolute;
            bottom: -.2rem;
            left: 0;
            right: 0;
            margin: 0 auto;
            font-family: v.$fuente_principal;
            font-weight: 400;
            font-size: 1.2rem;
        }
    }
}

.barra-progreso {
    position: sticky;
    top: 0;
    width: 100%;
    margin: 0 auto;
    padding: 1.5rem 0;
    background: #fff;
    z-index: 10;

    .progreso {
        margin: 0 auto;
        width: 15%;
        height: .2rem;
        background-color: v.$negro;
        border-radius: 1rem;
        transition: .3s ease;
    }
}

.alineador {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 1rem;

    @include m.tablet {
        flex-direction: row;
    }
}

.contenedor-maqueta {
    width: 90%;
    max-height: calc(100vh - 17rem);
    margin: .5rem auto;
    padding: 0 .5rem;
    background: v.$blanco;
    border-radius: 2rem;
    transition: all .3s ease;
    overflow-y: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
    
    @include m.telefono {
        max-height: calc(100vh - 20rem);
        width: fit-content;
        min-width: 50rem;
    }
    @include m.tablet {
        padding: 0 2rem;
    }
}

.paso {
    display: none;
    overflow-y: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
    padding: 0 1rem;
    
    @include m.telefono {
        // max-height: 60vh;
    }
    @include m.desktop {
        margin: 0 auto;
        // width: 60%;
    }

    &::-webkit-scrollbar {
        display: none;
    }

    &.activo {
        display: flex;
        flex-direction: column;
    }

    &.paso-3 {

        @include m.tablet {
            padding-left: 0;
        }
    }

    &.paso-5 {
        margin: 0 auto;

        @include m.tablet {
            max-width: 45rem;
        }

        @include m.tablet {
            margin-top: .5rem;
        }
    }

    &.paso-6 {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .sin-horarios, .cargando-horarios {
        text-align: center;
    }
}

.contenedor {
    width: 100%;

    .titulo, .subtitulo {
        text-align: left;
        margin: 1rem;
        font-weight: 500 !important;
        color: v.$neutral_12 !important;

        span {
            font-weight: 700 !important;
            color: v.$main-3 !important;
        }
    }

    .titulo {
        font-size: 2rem;
        margin-bottom: .2rem;
    }

    .subtitulo {
        color: v.$gris;
        text-transform: uppercase;
        font-size: 1.5rem;
        margin-bottom: 1rem;

        &.info-seleccion {
            text-align: center;
        }

        &.subtitulo-horarios {
            font-size: 1.7rem;
            text-transform: none;

            span {
                // color: v.$neutral_09 !important;
                color: v.$main-3 !important;
                font-weight: 500 !important;
            }
        }
    }

    .flotante {
        position: relative;
        background-color: v.$blanco;
        border-radius: 2rem;
        margin-bottom: .5rem;
        padding: 1rem;
        cursor: pointer;
        transition: all 0.3s ease;
        border: 1px solid transparent; // Borde transparente por defecto
        -webkit-tap-highlight-color: transparent;
        user-select: none;

        &:not(.seleccionado):hover {
            transform: translateY(-2px);
            box-shadow: 0 4px 8px rgba(0,0,0,0.05);
            transition: all 0.3s ease;
        }

        .check-icon {
            position: absolute;
            right: -.8rem;
            top: 50%;
            transform: translateY(-50%) scale(0);
            width: 2rem;
            height: 2rem;
            padding: 0.5rem;
            background: v.$verde-check;
            fill: v.$neutral_13;
            border-radius: 50%;
            opacity: 0;
            transition: opacity 0.2s, transform 0.3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
            z-index: 19;
        }

        &.seleccionado {
            border-color: v.$gris;
            transition: all .3s ease;

            .check-icon {
                opacity: 1;
                transform: translateY(-50%) scale(1);
                transition-delay: .2s;
            }
        }

        &.servicio {
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: .1rem solid v.$neutral_05;
            border-radius: 0;
            gap: 2rem;

            &:not(.seleccionado):hover {
                box-shadow: 0 4px 8px rgba(0, 0, 0, 0);
                border-bottom: .1rem solid v.$neutral_08;
            }

            &.descuento {
                background: #eccbf53f;
                border-radius: 1rem;

                .badge-promo {
                    position: absolute;
                    top: -.1rem;
                    left: calc(50% - 2.5rem);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: .2rem 1rem;
                    // width: 4rem;
                    // height: 2.7rem;
                    border-radius: .8rem;
                    background: #dbc6e3;
                    // border: solid .1rem v.$neutral_09;
                    font-size: 1rem;
                    font-weight: 700;
                    color: v.$neutral_00;
                }

                .detalles-servicio {

                    .precio-con-descuento {
                        font-weight: 700;
                        color: #444;

                        .precio-original {
                            font-weight: 300;
                            text-decoration: line-through;
                        }
                    }
                }

                &.seleccionado {
                    background: #eccbf54c;

                    .precio-con-descuento {
                        color: v.$neutral_12;

                        .precio-original {
                            color: v.$neutral_11;
                        }
                    }
                }
            }

            .p1 {
                text-align: left;
                font-weight: 600;
                font-size: 1.3rem;
                color: v.$neutral_11;
            }

            .detalles-servicio {
                display: flex;
                flex-direction: column;
                min-width: 6.7rem;
                text-align: right;
                // padding-left: 2rem;
                // border-left: 1px solid #c2c2c2;
                span {
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    gap: .3rem;
                    font-size: 1.4rem;
                    text-align: right;
                    color: #666666;
                }
            }

            &.seleccionado {
                border: .1rem solid v.$neutral_07;
                border-radius: 1rem;
                background-color: v.$neutral_02;

                .p1 {
                    color: v.$neutral_12;
                }

                .detalles-servicio {
                    span {
                        color: v.$neutral_13;
                    }
                }
            }
        }

        &.horario {
            width: 45%;
            border: .1rem solid v.$neutral_05;
            margin-bottom: .5rem;
            padding: .5rem;

            @include m.tablet {
                min-width: 15rem;
            }

            &:hover {
                border: .1rem solid v.$neutral_08;
                box-shadow: 0 4px 8px rgba(0, 0, 0, 0);
            }

            .p1 {
                width: 100%;
                text-align: center;
                color: v.$neutral_11;
            }

            &.seleccionado {
                background-color: v.$neutral_02;
                border: .1rem solid v.$neutral_07;

                .p1 {
                    color: v.$neutral_13;
                }
            }

            &.ocupado {
                opacity: .5;
                cursor: not-allowed;
                pointer-events: none;
                filter: grayscale(70%);
            }

            &.no-disponible {
                display: none;
            }
        }

        &.servicio p,
        &.horario p {
            color: v.$negro;
            text-align: center;
        }

        &.fecha {

            .p1 {
                width: 100%;
                text-align: center;
                color: v.$negro;
            }
        }

        .p1, .p2 {
            font-size: 1.4rem;
        }

        .p1 {
            text-transform: uppercase;
            color: v.$gris;
        }
    }

    .form-datos {
        width: 100%;
        margin: 0 auto;

        @include m.tablet {
            max-width: 50rem;
        }

        .grupo-form {
            position: relative;
            width: 100%;
            padding: 1rem;
            padding-top: 2.5rem;
            background-color: transparent;

            &:not(.grupo-entrega) {
                input:not([type="radio"]) {
                width: 100%;
                border: 0;
                border-bottom: 1px solid v.$neutral_05;
                outline: none;
                background: transparent;
                font-size: 1.5rem;
                color: v.$neutral_12;
                letter-spacing: 1px;
                padding: 10px 0;
                transition: 0.3s;
                }
            }

            label {
                position: absolute;
                display: flex !important;
                top: 3rem; /* Ajustado para alineación inicial */
                left: 1rem;
                font-size: 1.7rem;
                letter-spacing: 0;
                color: v.$neutral_10;
                transition: 0.3s ease all;
                pointer-events: none;
                transform-origin: top left;
                background-color: transparent;
            }

            small {
                font-size: 1rem;
                color: v.$neutral_12;
            }

            input:focus + label,
            input:not(:placeholder-shown) + label,
            input.has-content + label {
                transform: translateY(-2.5rem) scale(0.8);
                font-weight: 500;
                color: v.$main-3;
            }

            textarea.input-floating {
                width: 100%;
                border: 0;
                border-bottom: 1px solid v.$neutral_05;
                outline: none;
                background: transparent;
                font-size: 1.5rem;
                font-family: v.$fuente_principal;
                letter-spacing: .1rem;
                padding: 10px 0;
                transition: 0.3s;
                color: v.$negro;
                min-height: 60px; /* Altura mínima inicial */
            }

            /* Efecto flotante para textarea (igual que inputs) */
            textarea:focus + label,
            textarea:not(:placeholder-shown) + label,
            textarea.has-content + label {
                transform: translateY(-2.5rem) scale(0.8);
                color: v.$main-3;
                font-weight: 500;
            }
            
            .small-mensaje {
                color: v.$neutral_12;
                font-size: 0.8rem;
            }

            .invalido + label + .small-mensaje {
                color: #b83a3a;
            }
        }
    }

    .exito {
        display: flex !important;
        flex-direction: column;
        align-items: center;
        width: 100%;
        margin: 0 auto;

        @include m.tablet {
            max-width: 45rem;
        }
        
        .check-icon {
            width: 80px;
            height: 80px;
            position: relative;
            border-radius: 50%;
            box-sizing: content-box;
            border: 4px solid #4CAF50;
            
            &::before {
                top: 3px;
                left: -2px;
                width: 30px;
                transform-origin: 100% 50%;
                border-radius: 100px 0 0 100px;
            }
            
            &::after {
                top: 0;
                left: 30px;
                width: 60px;
                transform-origin: 0 50%;
                border-radius: 0 100px 100px 0;
                animation: rotate-circle 4.25s ease-in;
            }
            
            &::before, &::after {
                content: '';
                height: 100px;
                position: absolute;
                background: #FFFFFF;
                transform: rotate(-45deg);
            }
            
            .icon-line {
                height: 5px;
                background-color: #4CAF50;
                display: block;
                border-radius: 2px;
                position: absolute;
                z-index: 10;
                
                &.line-tip {
                    top: 46px;
                    left: 14px;
                    width: 25px;
                    transform: rotate(45deg);
                    animation: icon-line-tip 0.75s;
                }
                
                &.line-long {
                    top: 38px;
                    right: 8px;
                    width: 47px;
                    transform: rotate(-45deg);
                    animation: icon-line-long 0.75s;
                }
            }
            
            .icon-circle {
                top: -4px;
                left: -4px;
                z-index: 10;
                width: 80px;
                height: 80px;
                border-radius: 50%;
                position: absolute;
                box-sizing: content-box;
                border: 4px solid rgba(76, 175, 80, .5);
            }
            
            .icon-fix {
                top: 8px;
                width: 5px;
                left: 26px;
                z-index: 1;
                height: 85px;
                position: absolute;
                transform: rotate(-45deg);
                background-color: #FFFFFF;
            }
        }

        h2, p {
            text-align: center;
            color: v.$neutral_11;
        }

        h2 {
            font-family: v.$fuente_principal;
            letter-spacing: 0;
            font-size: 2rem;
            font-weight: 600;
        }

        p {
            font-size: 1.5rem;
            font-weight: 400;

            span {
                font-weight: 700;
                color: v.$neutral_12;
            }

            &.nos-vemos {
                font-weight: 700;
                color: v.$neutral_12;
            }
        }
    }
}

@keyframes rotate-circle {
    0% {
        transform: rotate(-45deg);
    }
    5% {
        transform: rotate(-45deg);
    }
    12% {
        transform: rotate(-405deg);
    }
    100% {
        transform: rotate(-405deg);
    }
}

@keyframes icon-line-tip {
    0% {
        width: 0;
        left: 1px;
        top: 19px;
    }
    54% {
        width: 0;
        left: 1px;
        top: 19px;
    }
    70% {
        width: 50px;
        left: -8px;
        top: 37px;
    }
    84% {
        width: 17px;
        left: 21px;
        top: 48px;
    }
    100% {
        width: 25px;
        left: 14px;
        top: 45px;
    }
}

@keyframes icon-line-long {
    0% {
        width: 0;
        right: 46px;
        top: 54px;
    }
    65% {
        width: 0;
        right: 46px;
        top: 54px;
    }
    84% {
        width: 55px;
        right: 0px;
        top: 35px;
    }
    100% {
        width: 47px;
        right: 8px;
        top: 38px;
    }
}