@use "../base/variables" as v;
@use "../base/mixins" as m;

.resumen-continuo {
  width: 100%;
  height: max-content;
  padding: 1rem 2rem;
  border-bottom: .1rem solid v.$neutral_04;

  @include m.tablet {
    max-width: 22rem;
    height: auto;
    padding: 0 0 1rem 1rem;
    border-bottom: 0;
  }

  .resumen-content {
    position: sticky;
    top: 4.2rem;
    width: 100%;

    .resumen-servicios {

      .servicios-lista {
        font-size: 1.4rem;

        .servicio-item {
          display: flex;
          align-items: center;
          gap: 0.3rem;
          font-weight: 500;

          svg {
            stroke: v.$main-3;
            fill: v.$main-3;
          }
        }
      }

      .servicios-totales {
        display: flex;
        gap: 1rem;
        width: 100%;

        @include m.tablet {
          flex-direction: column;
          gap: .5rem;
        }

        p {
          display: flex;
          align-items: center;
          gap: .2rem;
          margin: .5rem 0;
          font-size: 1.4rem;
          font-weight: 500;
          color: #555;

          @include m.tablet {
            margin: 0;
            margin-top: 0.5rem;
          }
        }
      }
    }

    .resumen-fecha-hora {
      display: flex !important;
      gap: 1rem;
      margin-top: 0.5rem;

      @include m.tablet {
        flex-direction: column;
      }

      p {
        display: flex;
        align-items: center;
        gap: .5rem;
        font-size: 1.4rem;
        font-weight: 500;
        color: v.$neutral_11;
      }
    }
  }
}

.sin-datos {
  color: #6c757d;
  font-style: italic;
  margin: 10px 0;
}

.btn-reservar-final {
  background: #28a745;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 12px 20px;
  font-weight: 500;
  cursor: pointer;
  width: 100%;
  transition: background 0.2s;
}

.btn-reservar-final:hover {
  background: #218838;
}

.especialistas-resumen {
  position: relative !important;
  display: flex !important;
  flex-direction: column;
  margin: 10px 0;
  overflow: visible !important;
  width: 100% !important;
  max-height: fit-content !important;
}

.especialista-item {
  display: flex;
  align-items: center;
  width: 100%;
}

.avatar-especialista {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  margin-right: 8px;
  object-fit: cover;
}

.resumen-especialistas h4 {
  margin-bottom: 8px;
  color: #495057;
  font-size: 1rem;
}