@use "../base/variables" as v;
@use "../base/mixins" as m;

.categoria-especialistas {
    width: 100%;
    border-radius: 12px;
    padding: 1.5rem 1rem;
    background: white;

    @include m.tablet {
        min-width: 45rem;
    }

    h3 {
        margin: 0 0 1rem 0;
        font-family: v.$fuente_principal;
        font-size: 1.1rem;
        color: #333;
        font-weight: 500;
        letter-spacing: 0;

        span {
            color: v.$neutral_13;
            font-weight: 700;
        }
    }
}


.seleccion-predeterminada {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    min-height: 7rem;
    cursor: pointer;
    transition: all 0.3s ease;
    border: 1px solid v.$neutral_05;
    border-radius: 1rem;
    // border-bottom: .1rem solid v.$neutral_05;
    position: relative;
    transition: background .3s ease;

    &:hover {
        background: v.$main-2;
        border-radius: 1rem;

        .texto-predeterminado {
            color: v.$main-1;
        }

        .acordeon-icon {
            stroke: v.$main-1;
        }
    }
}

.seleccion-predeterminada.seleccionado {
    background: v.$neutral-02;
    border: .1rem solid v.$neutral_07;

    &:hover {

        .texto-predeterminado {
            color: v.$neutral_12;
        }

        .acordeon-icon {
            stroke: v.$main-2;
        }
    }

    .texto-predeterminado {
        color: v.$neutral_12;
    }

    .acordeon-icon {
        stroke: v.$main-4;
    }
}

.seleccion-predeterminada .info-especialista {
    display: flex;
    align-items: center;
    gap: 1rem;
    flex: 1;
    margin-right: 1rem;
}

.seleccion-predeterminada {
    display: flex;
    gap: .5rem;
}

.seleccion-predeterminada .avatar-especialista {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.seleccion-predeterminada .nombre-especialista {
    font-weight: 500;
    color: v.$neutral_12;
}

.seleccion-predeterminada .texto-predeterminado {
    color: v.$neutral_10;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 1.4rem;
    // font-style: italic;
}

.botones-especialista {
    display: flex;
    align-items: center;
}

.btn-ver-especialistas {
    background: none;
    border: none;
    padding: 8px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #333; /* Color para el SVG */
}

.acordeon-icon {
    transform: rotate(45deg);
    transition: transform 0.3s ease !important;
    stroke: v.$main-2;
}

.acordeon-icon.girado {
    transform: rotate(0) !important;
    stroke: v.$main-4;
}

/* Asegurar que el SVG herede el color */
.acordeon-icon {
    color: inherit;
}

.especialistas-lista {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;
    margin-top: 1rem;
    padding-right: 1rem;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s ease;
}

.especialistas-lista.abierto {
    max-height: 500px;
}

.flotante.especialista {
    display: flex;
    align-items: center;
    padding: 1rem;
    border-radius: 0;
    border-bottom: .1rem solid v.$neutral_05;
    cursor: pointer;
    transition: all 0.3s ease;
}

.flotante.especialista:hover {
    // border-color: v.$neutral_05;
    border-radius: 1rem;
    transform: translateY(-2px);
    background: v.$main-1;
}

.flotante.especialista.seleccionado {
    border-color: v.$neutral_07;
    background: v.$neutral-02;
    border-radius: 1rem;
}

.flotante.especialista .avatar-especialista {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 1rem;
}

.flotante.especialista .info-especialista {
    flex: 1;
}

.flotante.especialista .p1 {
    margin: 0;
    font-weight: 500;
    color: v.$neutral_12;
}
        
/* Nuevos estilos para mejoras visuales */
.seleccion-predeterminada .badge-recomendado {
    position: absolute;
    top: -8px;
    right: -8px;
    background: #ff9800;
    color: white;
    font-size: 0.7rem;
    padding: 2px 8px;
    border-radius: 12px;
    font-weight: 500;
}
        
.seleccion-predeterminada .info-disponibilidad {
    font-size: 0.85rem;
    color: #666;
    margin-top: 4px;
}
        
.seleccion-predeterminada .icono-disponibilidad {
    display: inline-flex;
    align-items: center;
    margin-right: 6px;
}
        
.especialista-info-adicional {
    font-size: 0.85rem;
    color: #666;
    margin-top: 4px;
}
        
.especialista-calificacion {
    display: flex;
    align-items: center;
    margin-top: 4px;
}

.especialista-calificacion .estrella {
    color: #ffc107;
    margin-right: 2px;
    font-size: 0.9rem;
}

.btn-buscar-disponibilidad {
    background: transparent;
    border: 1px solid #ff9800;
    color: #ff9800;
    padding: 0.5rem;
    border-radius: 6px;
    cursor: pointer;
    margin-right: 0.5rem;
    transition: all 0.3s ease;
    
    &:hover {
        background: #ff9800;
        color: white;
    }
    
    svg {
        fill: currentColor;
    }
}


.opcion-disponibilidad {
    background-color: v.$main-1 !important;
    margin-top: .5rem;
}

.opcion-disponibilidad:hover {
    border-color: #adb5bd;
}

.opcion-disponibilidad .detalles-especialista {
    font-size: 0.85rem;
    color: #6c757d;
    margin-top: 0.25rem;
}