@use "base/variables" as v;
@use "base/mixins" as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    overflow-x: hidden;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    line-height: 1.8;
    color: v.$neutral-12;
    overflow-x: hidden;
    height: 100vh;
    background-color: v.$neutral_03;
    margin: 0;
    scroll-behavior: smooth;
    letter-spacing: 0;

    &.body-admin {
        height: auto;
    }
}

.contenedor {
    width: 95%;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    // font-family: v.$fuente_titulo;
    font-weight: 300;
}

p {
    margin: 0;
    font-weight: 300;
}

button, a, .flotante, [role="button"] {
  -webkit-tap-highlight-color: transparent;
}